  border-bottom: 0.0625rem solid rgba(255, 255, 255, 0.1);
}

.g-hor-divider__solid--dark-1-light {
  border-bottom: 0.0625rem solid rgba(35, 35, 35, 0.4);
}

.g-hor-divider__solid--dark-1-lightest {
  border-bottom: 0.0625rem solid rgba(35, 35, 35, 0.1);
}

.g-hor-divider__solid--gold {
  border-bottom: 0.0625rem solid #b99769;
}

.g-hor-divider__solid--primary {
  border-top: 0.25rem solid #4a4ace;
}

.g-hor-divider__solid--primary-lighter {
  border-top: 0.125rem solid rgba(74, 74, 206, 0.6);
}

.g-ver-divider__solid--white-opacity-lightest {
  border-right: 0.0625rem solid rgba(255, 255, 255, 0.1);
}

.g-ver-divider__solid--sepia-light {
  border-right: 0.0625rem solid rgba(118, 99, 69, 0.6);
}

/*----------------------------------
  Border
------------------------------------*/
.g-brd__solid-1--white {
  border: 0.0625rem solid #fff;
}

.g-brd__solid-7--white {
  border: 0.438rem solid #fff;
}

.g-brd__solid-7--yellow {
  border: 0.438rem solid #fff43e;
}

.g-brd__solid-3--gold {
  border: 0.1875rem solid #b99769;
}

.g-brd__solid-3--gold-light {
  border: 0.1875rem solid rgba(185, 151, 105, 0.6);
}

.g-brd__solid-2--gold-lightest {
  border: 0.125rem solid rgba(185, 151, 105, 0.2);
}

/*----------------------------------
  Box Shadow
------------------------------------*/
.g-box-shadow__primary-v1 {
  box-shadow: 0 0.1875rem 0.5rem -0.0625rem rgba(74, 74, 206, 0.3);
}

.g-box-shadow__primary-v2 {
  box-shadow: 0 1rem 2rem -0.5rem rgba(74, 74, 206, 0.7);
}

.g-box-shadow__primary-v3 {
  box-shadow: 0 0.125rem 0.1875rem rgba(74, 74, 206, 0.6);
}

.g-box-shadow__dark-1-lightest-v1 {
  box-shadow: 0 0.125rem 0.1875rem rgba(35, 35, 35, 0.13);
}

.g-box-shadow__dark-1-lightest-v2 {
  box-shadow: 0 1rem 1.5rem rgba(35, 35, 35, 0.05);
}

.g-box-shadow__dark-1-lightest-v3 {
  box-shadow: 0 1rem 1.5rem rgba(35, 35, 35, 0.035);
}

/*----------------------------------
  Box Shadow on Hover
------------------------------------*/
/* Dark 1 Lightest */
.g-box-shadow-hover__dark-1-lightest-v1 {
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.g-box-shadow-hover__dark-1-lightest-v1:hover {
  box-shadow: 0 0.5rem 1rem rgba(35, 35, 35, 0.075);
}

/* Dark 1 Lightest */
.g-brd-hover__gold-light-v1 {
  border: .125rem solid transparent;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.g-brd-hover__gold-light-v1:hover {
  border-color: rgba(185, 151, 105, 0.6);
}

.g-brd-hover__primary {
  border: .125rem solid transparent;
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.g-brd-hover__primary:hover {
  border-color: rgba(74, 74, 206, 0.6);
}

/*----------------------------------
  Text Shadow
------------------------------------*/
.g-text-shadow__primary-v1 {
  text-shadow: 0.1875rem 0.1875rem 0.1875rem rgba(74, 74, 206, 0.6);
}

