/* Hover Colors
------------------------------ */
.g-color--white-opacity[href],
.g-color--white-opacity-lighter[href],
.g-color--white-opacity-light[href] {
  transition-duration: 300ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}

.g-color--white-opacity[href]:focus, .g-color--white-opacity[href]:hover,
.g-color--white-opacity-lighter[href]:focus,
.g-color--white-opacity-lighter[href]:hover,
.g-color--white-opacity-light[href]:focus,
.g-color--white-opacity-light[href]:hover {
  color: #fff;
}

