/* Overlay Colors
------------------------------ */
/* Primary Left to Right */
.g-overlay-color--primary-ltr:before {
  background-image: linear-gradient(to bottom, rgba(74, 74, 206, 0.9) 0%, #4a4ace 100%);
  background-repeat: repeat-x;
}

/* Dark Light Color */
.g-overlay-color--dark-1-light:before {
  background: rgba(35, 35, 35, 0.7);
  z-index: 0;
}

/* Dark Lightest Color */
